.main-nav{
  display: flex;
  flex-direction: column;  
  align-items: center;
  background-color: #191919;
  position: sticky;
  margin-bottom: 10px;
    
}
.main-nav__items {
    display: inline-flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    
  }
.main-nav__item {
  float: left;
  padding: 8px;
  height:30px;
  
 
}
.hyperlink{
  text-decoration: none;
  color:rgb(205, 130, 100);
  font-weight: bold;
  padding: 3px 0px;
}
.hyperlink:hover,.hyperlink:active
{
  color:rgb(255, 234, 0);
  border-bottom: 2px solid yellow;
}