
.panel-wrapper{
    display: flex;
    flex-direction: column;
    margin:0;
    border-bottom: 2px rgba(255, 149, 0, 0.467) solid;  
}

.title-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 2px rgba(255, 149, 0, 0.467) solid;
}
.section-title{
    display: flex;   
    padding: 0;
    margin:0;
    margin-left: 10px;
    font-size: 45px;
    color: rgb(255, 162, 0);
    align-items: center;
    align-self: center;    
}
.sub-title{
    display: flex;   
    padding: 0;
    margin:0;
    margin-left: 10px;
    font-size: 40px;
    align-items: center;
    align-self: center;    
}

