/* Container Area */

.footer-container{
    display: flex;
    height: 100px;
    border-top: 2px solid grey;
}

.left{
    width: 80%;
    display: inline-flex;
    flex-direction: row;
    align-self: center;
    height:50px;
}
.icon{
    max-width:100%;
max-height:100%;
filter: brightness(0) invert(1);
}
.right{
    display: inline-flex;
    flex-direction: row;
    align-self: center;
}

/* Container Area */

.ul{
    list-style:none;
    display: inline-flex;
    margin: 0;
    padding: 0;
    margin-left: 60px;
   
}
.li{
    margin-left: 25px;
    list-style:none;
    text-decoration: none;
    height: 50px;
    
}
.a{
    text-decoration: none;
}
