.content-imgOrvid{
    display: flex;
    flex-direction: column;
       
   
    padding: 1px
   
}

.imgOrvid{
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: center;
    width: 800px;
    
    max-height: 600px;  
    box-shadow: 0px 0px 18px 0px rgba(238, 146, 48, 0.75);
    margin-bottom: 10px;
}
.img-desc{
    display: flex;
    flex-direction: column;
    max-width: 100%;
    height: auto;   
   

}
.img-comment{
    display: flex;
    flex-direction: row;
    align-self: center;
    margin:0;
    padding: 0;
    font-size: small;
    font-style: italic;
    margin-bottom: 5px;
}
.hyperlink{
    text-decoration: none;
    font-size: 15px;
    margin-left: 5px;  
    color:rgb(217, 255, 0);
    font-weight: bold;      
}