*{
  box-sizing: border-box;
}
.App {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
 
}
.center-container{
  width: 1400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-self: center;
  
}
.container-title{
  display: flex;
  align-self:center;
  margin-left:10px;
  height: 55px;
  width: 100%;
  border-bottom: 3px solid rgb(255, 242, 0);
  margin-top: 15px;
  
}
.tagTitle{
  margin:0px;

  padding: 0px;
  
  height: 40px;
 
  width: fit-content;
}

.h3,h2{
  border-bottom: 2px rgb(255, 72, 0) solid;
  margin: 10px;
}


.App-link {
  color: #61dafb;
}

.content-container{
  display: flex;

  flex-direction: column;
  align-items: center;
  justify-content: center;
  
}

.desc-container{
display: inline-flex;
width: 100%;

}
.skill-container{
display: flex;
flex-direction: column;
padding: 10px 0px;
margin-top: 5px;

}

.experience-container{
  display: flex;
  margin-left:20px
}
.history-container{

  width: 100%;
  margin-top: 10px;
  margin-bottom: 0px;
  
}

