.history-container{
    display: inline-flex;
    text-align: right;
    list-style: none;
    align-items: center;
    align-self: flex-start;
    width: 100%;
    
   
}
.date-container{
    display: inline-flex;
    
    height: 150px;
    width: 150px;
}
.date-text{
    margin: 20px;
}
.details-container{
    
    margin-left: 10px;
    display: inline-flex;
    border-left: 2px solid rgb(255, 30, 0);
    margin-left: 5px;
    height: 180px;
    width: 100%;
    text-align: left;
}
.details-content{
    
    display: flex;
    height: inherit;
    width: inherit;
    flex-direction: row;
}
.details-text-container{
  
    display: inline-flex;
    flex-direction: row;
    height: inherit;
    width: 80%;
}
.details-text-container-no-image{
   
    display: inline-flex;
    flex-direction: row;
    height: inherit;
    border-right: 2px solid grey;;
    width: 100%;
}
.details-text-wrapper{
    
    display: flex;
    flex-direction: column;
    width: 100%;
}
.details-text,.details-text-charge,.details-text-title,.details-text-desc,.details-text-hyperlink{
    padding: 0;
    margin: 0;
}
.details-text-title{
    margin-left: 5px;
    margin-bottom: 5px;
}
.details-text-charge{
    margin-left: 10px;
    font-size: 15px;
    margin-bottom: 5px;
}
.details-text-desc{
    margin-left: 5px;
    font-size: 15.5px;
    
}
.details-text-hyperlink{
    top: 25px;
    font-size: 15px;
    position: relative;
        
}
.title-segment{
    display: flex;
    flex-direction: column;
    height: 25%;
    width: 100%;
    border-top: 2px solid grey;
}

.charge-segment{
    display: flex;
    flex-direction: column;
    height: 15%;
    width: 100%;
    
}
.desc-segment{
    display: flex;
    flex-direction: column;
    height: 40%;
    width: 100%;
    margin-bottom: 5px;
   
}
.read-more-segment{
    display: flex;
    flex-direction: column;
    height: 20%;
    width: 100%;
    border-top: 3px solid rgba(246, 255, 0, 0.805);
}

.hyperlink{
    
        text-decoration: none;
        color:rgb(255, 72, 0);
        font-weight: bold;        
        margin-bottom: 10px;
        font-size: 14px;
    
}
.hyperlink-not-ready{
    
    text-decoration: none;
    color:rgb(255, 72, 0);
    font-weight: bold;        
    margin: 0;
    margin-top: 5px;
    margin-bottom: 10px;
    font-size: 14px;

}
.hyperlink > .hyperlink:last-child{
    border-bottom: 6px solid grey;
}

.details-img{
    display: inline-flex;    
    align-self: center;
    height: inherit;
    width: 25%;
    border-top: 2px solid grey;
    border-left: 2px solid grey;
    border-right: 2px solid grey;
}

.center {
    display: block;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
  }
.button {
    transition-duration: 0.4s;
    background-color: #f44336;
    color: rgb(255, 255, 255);

    font-size: 15px;
    border: 2px solid #020202; /* Green */
    border-radius: 8px;
    
  }
  
.button:hover {
    background-color: #661e19; /* Green */
    color: white;
  }