.modal-body{
    height: 100%;
    width: 100%;
    position: fixed;
   
}
.overlay{
    background-color: rgba(0,0,0,0.5);
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 5;
  }

  .modal-container{
    display: flex;
    flex-direction: column;
    max-width: 75%;
    max-width: 1400px;
    width: 100%;
    position: fixed;
    top: 50%;
    left: 50%;
    height:99%;
    transform: translate(-50%,-50%);
    background-color: #616060;
    box-shadow: 0px 0px 18px 0px rgba(238, 146, 48, 0.75);
    border-radius: 5%;
    z-index: 20;
    overflow: auto;
}
.title-container{
    display: flex;
    width: 100%;
    height: 10%;
    align-self: center;
    justify-content: center;
    align-items: center;
    text-align: center;
    
    border-bottom: 1px gold solid;
}
.close-button{
    
  position: absolute;
  inset: 20px 20px auto auto;
  cursor: zoom-out;
  z-index: 1;
  filter: brightness(0) invert(1);

}
.fixed-pos{
    position: fixed;
}
.remove-margin{
    margin: 0;
    padding: 0;
}
.image-portrait{

    margin-top: 5px;
    margin-bottom: 5px;
    padding: 5px;
    max-width: 100%;
    height: auto;
    object-fit: cover;
}
.portrait{
    width: 100%;
    height: auto;
}